// radio with images for labels
.form-table {
    .launchpad-layout-options {
        ul {
            li {
                float: left;
                margin-right: 20px;

                &:last-child {
                    margin-right: 0;
                }

                input[type="radio"].image-replaced-option {
                    display: none;
                }

                input[type="radio"].image-replaced-option + span {
                    display: inline-block;
                    img {
                        display: block;
                    }
                }

                input[type="radio"].image-replaced-option:checked + span {
                    background: $primary-color;
                }
            }
        }

    }

    textarea {
        width: 100%;
        min-height: 200px;

        &.import-export {
            height: 80px;
            margin-top: 5px;
            min-height: 80px;
        }
    }

}